.login_page {
    height: 100vh;
    width: 100%;
    padding-top: 50px;
}

.login_page.active {
    background-color: #3c3c3c;
}

.login_page .container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.login_page .login_page_head {
    text-transform: uppercase;
    font-size: 24px;
    font-family: cursive;
}

.login_page .login_page_head.active {
    color: #ffffff;
}

.login_page .avatar {
    width: 8rem;
    height: 8rem;
    border-radius: 50%;
    overflow: hidden;
}

.login_page .form_content {
    display: flex;
    flex-direction: column;
    width: 20rem;
}

.login_page .form_content .email.active {
    color: #ffffff;
}
.login_page .form_content .emailIp.active {
    background-color: #3c3c3c;
    color: #ffffff;
    border: 1px solid #ffffff;
}

.login_page .form_content .password.active {
    color: #ffffff;
}
.login_page .form_content .passwordIp.active {
    background-color: #3c3c3c;
    color: #ffffff;
    border: 1px solid #ffffff;
}

.login_page .form_content label {
    margin-top: 10px;
    font-size: 16px;
    margin-bottom: 5px;
}

.login_page .form_content input {
    width: 100%;
    font-size: 15px;
    padding: 10px 5px;
    border: 1px solid #000000;
    border-radius: 5px;
}

.login_page .form_content input:focus {
    box-shadow: 0rem 0rem .8rem 0rem #f8d511;
}

.login_page button {
    margin-top: 10px;
    background-color: #f8d511;
    color: #000000;
    padding: 10px 14px;
    font-size: 16px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    font-weight: 500;
}

.login_page button:hover {
    opacity: .7;
}

.login_page .container .navigate {
    font-size: 16px;
    text-transform: capitalize;
}

.login_page .container .navigate.active {
    color: #ffffff;
}

.login_page .container p span a {
    color: #ff8a04;
    font-weight: 600;
    text-decoration: none;
}