.dashboard_page {
    height: auto;
    width: 100%;
}

.dashboard_page.active {
    background-color: #3c3c3c;
}

.dashboard_page .dashboard_page_container {
    height: 100%;
    width: 100%;
    display: flex;
}

/* Left Panel */
.dashboard_page .dashboard_page_container .dashboard_page_container_left {
    height: 100%;
    width: 80%;
    flex: 9;
    padding-top: 70px;
}

/* First Div */
.dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_frist {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
    gap: 1.5rem;
    margin: 0px 30px;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_frist .dashboard_page_frist_box {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    box-shadow: 0rem 0rem 0.7rem 0rem #807e7e;
    border-radius: 10px;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_frist .dashboard_page_frist_box .dashboard_page_frist_title {
    font-size: 25px;
    font-family: cursive;
    font-weight: 600;
    text-transform: capitalize;
    margin-bottom: -10px;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_frist .dashboard_page_frist_box .dashboard_page_frist_title.active {
    color: #ffffff;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_frist .dashboard_page_frist_box .dashboard_page_frist_container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 100%;
    width: 100%;
    margin-bottom: -25px;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_frist .dashboard_page_frist_box .dashboard_page_frist_container .dashboard_page_frist_num {
    font-size: 22px;
    font-weight: 600;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_frist .dashboard_page_frist_box .dashboard_page_frist_container .dashboard_page_frist_num.active {
    color: #ffffff;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_frist .dashboard_page_frist_box .dashboard_page_frist_container .dashboard_page_frist_percen {
    font-size: 14px;
    display: flex;
    align-items: center;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_frist .dashboard_page_frist_box .dashboard_page_frist_container .dashboard_page_frist_percen.active {
    color: #ffffff;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_frist .dashboard_page_frist_box .dashboard_page_frist_discription {
    color: #a7a3a3;
}

/* Second Div */
.dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_second {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px 30px;
    padding: 20px;
    box-shadow: 0rem 0rem .7rem 0rem #807e7e;
    border-radius: 10px;
    overflow: hidden;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_second .dashboard_second_progress_box {
    display: flex;
    flex-direction: column;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_second .dashboard_second_progress_box .dashboard_second_progress_title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: -10px;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_second .dashboard_second_progress_box .dashboard_second_progress_title .dashboard_second_progress_header {
    font-size: 20px;
    font-weight: 600;
    font-family: cursive;
    text-transform: capitalize;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_second .dashboard_second_progress_box .dashboard_second_progress_title .dashboard_second_progress_header.active {
    color: #ffffff;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_second .dashboard_second_progress_box .dashboard_second_progress_title .dashboard_second_progress_percen {
    font-size: 15px;
    font-weight: 600;
    font-family: cursive;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_second .dashboard_second_progress_box .dashboard_second_progress_title .dashboard_second_progress_percen.active {
    color: #ffffff;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_second .dashboard_second_progress_box .dashboard_second_progress_bar {
    width: 20rem;
    height: .8rem;
    background-color: #f3f3f3;
    border-radius: .5rem;
    position: relative;
}

/* Right Panel */
.dashboard_page .dashboard_page_container .dashboard_page_container_right {
    flex: 3;
    box-shadow: 0rem 0rem 0.8rem 0rem #807e7e;
    padding-top: 62px;
    /* min-height: 100vh; */
    width: 100%;
    overflow-y: scroll;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_right::-webkit-scrollbar {
    width: 0.5rem;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_right::-webkit-scrollbar-track {
    background: transparent;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_right::-webkit-scrollbar-thumb {
    background-color: #807e7e;
    border-radius: 5rem;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_right .dashboard_page_container_right_lists {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: auto;
    text-transform: capitalize;
    font-size: 18px;
    color: #4d4c4c;
    transition: linear 0.3s;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_right .dashboard_page_container_right_lists:hover {
    background-color: #4d4c4c;
    color: #f8f6f6;
}


.dashboard_page .dashboard_page_container .dashboard_page_container_right .dashboard_page_container_right_lists.active {
    color: #f8f6f6;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_right .dashboard_page_container_right_lists.active:hover {
    color: #3c3c3c;
    background-color: #f8f6f6;
}

@media (max-width: 991px) {
    .dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_second {
        flex-direction: column;
    }
}

@media (max-width: 768px) {
    .dashboard_page .dashboard_page_container .dashboard_page_container_right .dashboard_page_container_right_lists {
        font-size: 15px;
    }

    .dashboard_page .dashboard_page_container .dashboard_page_container_right {
        flex: 0;
        display: none;
    }

    .drawer_admin_right_header {
        text-align: center;
        font-size: 30px;
        font-family: cursive;
    }

    .drawer_admin_right_list .drawer_admin_right_list_link {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
        height: auto;
        text-transform: capitalize;
        font-size: 18px;
        color: #4d4c4c;
        cursor: pointer;
        margin: 5px;
        transition: linear 0.3s;
    }

    /* .drawer_admin_right_list .drawer_admin_right_list_link:hover {
        color: #f8f6f6;
        background: #4d4c4c;
    }

    .drawer_admin_right_list .drawer_admin_right_list_link.active {
        color: #f8f6f6;
    }
    
    .drawer_admin_right_list .drawer_admin_right_list_link.active:hover {
        color: #4d4c4c;
        background: #f8f6f6;
    } */
}

@media (max-width: 558px) {
    .dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_frist .dashboard_page_frist_box .dashboard_page_frist_title {
        font-size: 15px;
    }

    .dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_frist .dashboard_page_frist_box .dashboard_page_frist_container .dashboard_page_frist_num {
        font-size: 14px;
    }

    .dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_frist .dashboard_page_frist_box .dashboard_page_frist_container .dashboard_page_frist_percen {
        font-size: 12px;
    }

    .dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_frist .dashboard_page_frist_box .dashboard_page_frist_discription {
        font-size: 10px;
    }

    .dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_second {
        height: auto;
    }

    .dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_second .dashboard_second_progress_box .dashboard_second_progress_bar {
        width: auto;
        height: .4rem;
    }

    .dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_second .dashboard_second_progress_box .dashboard_second_progress_title .dashboard_second_progress_header {
        font-size: 15px;
    }

    .dashboard_page .dashboard_page_container .dashboard_page_container_left .dashboard_page_second .dashboard_second_progress_box .dashboard_second_progress_title span {
        font-size: 12px;
    }

    .dashboard_page .dashboard_page_container .dashboard_page_container_left {
        flex: 1;
    }
}

@media (max-width: 450px) {
    .dashboard_page .dashboard_page_container .dashboard_page_container_right .dashboard_page_container_right_lists {
        font-size: 10px;
    }
}