.profile_page {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 70px;
    padding-bottom: 20px;
}

.profile_page.active {
    background-color: #3c3c3c;
}

.profile_page .container {
    width: 40rem;
    height: 100%;
    border-radius: 10px;
    box-shadow: 0rem 0rem 0.6rem 0rem #a1a1a1;
    padding: 10px;
}

.profile_page .container .profile_page_title {
    font-size: 25px;
    font-weight: 600;
    font-family: cursive;
    text-transform: capitalize;
    padding-left: 10px;
}

.profile_page .container .profile_page_title.active {
    color: #ffffff;
}

.profile_page .container .profile_frist {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 11rem;
    padding: 10px;
}

.profile_page .container .profile_frist .profile_img_container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.profile_page .container .profile_frist .profile_img_container .profile_img {
    width: 9rem;
    height: 9rem;
}

.profile_page .container .profile_frist .profile_img_container .profile_img img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
}

.profile_page .container .profile_frist .profile_img_container .profile_img_update {
    width: 7rem;
    height: 2rem;
    border: none;
    background-color: #eee7e7;
    color: #ff6702;
    font-size: 14px;
    text-transform: capitalize;
    font-weight: 500;
    margin-top: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    cursor: pointer;
}

.profile_page .container .profile_frist .profile_img_container .profile_img_update:hover {
    letter-spacing: 1px;
    transition: all .3s linear;
}

.profile_page .container .profile_frist .profile_info_creator {
    padding-left: 10px;
}

.profile_page .container .profile_frist .profile_info_creator .profile_info_creator_title {
    font-size: 20px;
    font-weight: 600;
    font-family: cursive;
    text-transform: capitalize;
}

.profile_page .container .profile_frist .profile_info_creator .profile_info_creator_title.active {
    color: #ffffff;
}

.profile_page .container .profile_frist .profile_info_creator .profile_info_creator_email {
    font-size: 16px;
}

.profile_page .container .profile_frist .profile_info_creator .profile_info_creator_email.active {
    color: #ffffff;
}

.profile_page .container .profile_frist .profile_info_creator .profile_info_creator_created{
    font-size: 16px;
}

.profile_page .container .profile_frist .profile_info_creator .profile_info_creator_created.active{
    color: #ffffff;
}

.profile_page .container .profile_frist .profile_info_creator p .subscription_btn {
    width: 7rem;
    height: 2rem;
    border: none;
    background-color: #f8d511;
    color: #000000;
    font-size: 14px;
    text-transform: capitalize;
    font-weight: 500;
    border-radius: 5px;
    cursor: pointer;
}

.profile_page .container .profile_frist .profile_info_creator p .subscription_btn:hover {
    letter-spacing: 1px;
    transition: all .3s linear;
}

.profile_page .container .profile_frist .profile_info_creator .profile_info_creator_btns {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 10px;
}

.profile_page .container .profile_frist .profile_info_creator .profile_info_creator_btns .btn1 {
    width: 10rem;
    height: 2rem;
    border: none;
    background-color: #f8d511;
    color: #000000;
    font-size: 14px;
    text-transform: capitalize;
    font-weight: 500;
    border-radius: 5px;
    cursor: pointer;
}

.profile_page .container .profile_frist .profile_info_creator .profile_info_creator_btns .btn1:hover {
    letter-spacing: 1px;
    transition: all .3s linear;
}

.profile_page .container .profile_frist .profile_info_creator .profile_info_creator_btns .btn2 {
    width: 10rem;
    height: 2rem;
    border: none;
    background-color: #eee7e7;
    color: #ff6702;
    font-size: 14px;
    text-transform: capitalize;
    font-weight: 500;
    border-radius: 5px;
    cursor: pointer;
}

.profile_page .container .profile_frist .profile_info_creator .profile_info_creator_btns .btn2:hover {
    letter-spacing: 1px;
    transition: all .3s linear;
}

.profile_second .profile_second_info{
    transition:  all 0.3s linear;
}

.profile_second .profile_second_info:hover{
    transform: scale(0.9);
}

.profile_page .container .profile_second_title {
    font-size: 25px;
    font-weight: 600;
    font-family: cursive;
    text-transform: capitalize;
    padding-left: 10px;
}

.profile_page .container .profile_second_title.active {
    color: #ffffff;
}

.profile_page .container .profile_second {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
    gap: 1rem;
    margin-top: 20px;
    margin-bottom: 15px;
}

.profile_page .container .profile_second .profile_second_info {
    width: auto;
    height: "100%";
    border-radius: 5px;
    margin: "30px 0px";
    box-shadow: 0rem 0rem 0.6rem #cfcfcf;
}

.profile_page .container .profile_second .profile_second_info .profile_second_img {
    width: auto;
    height: 8rem;
}

.profile_page .container .profile_second .profile_second_info .profile_second_img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
}

.profile_page .container .profile_second .profile_second_info .profile_second_title {
    font-size: 20px;
    font-family: cursive;
    margin-bottom: -15px;
}

.profile_page .container .profile_second .profile_second_info .profile_second_description {
    font-size: 15px;
    padding: 8px;
    margin-bottom: -25px;
}

.profile_page .container .profile_second .profile_second_info .profile_second_description.active {
    color: #ffffff;
}

.profile_page .container .profile_second .profile_second_info .profile_second_category {
    font-size: 16px;
    text-transform: capitalize;
    font-weight: 600;
    font-family: cursive;
    margin-bottom: -10px;
    padding: 8px;
}

.profile_page .container .profile_second .profile_second_info .profile_second_category.active {
    color: #ffffff;
}

.profile_page .container .profile_second .profile_second_info .profile_second_btns {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px;
}

.profile_page .container .profile_second .profile_second_info .profile_second_btns .btn1 {
    width: 10rem;
    height: 2rem;
    border: none;
    background-color: #f8d511;
    color: #000000;
    font-size: 14px;
    text-transform: capitalize;
    font-weight: 500;
    border-radius: 5px;
    cursor: pointer;
}

.profile_page .container .profile_second .profile_second_info .profile_second_btns .btn1:hover {
    letter-spacing: 1px;
    transition: all .3s linear;
}

.profile_page .container .profile_second .profile_second_info .profile_second_btns .buy_course {
    width: 10rem;
    height: 2rem;
    border: none;
    background-color: #eee7e7;
    color: #ff6702;
    font-size: 14px;
    text-transform: capitalize;
    font-weight: 500;
    border-radius: 5px;
    cursor: pointer;
}

.profile_page .container .profile_second .profile_second_info .profile_second_btns .buy_course:hover {
    letter-spacing: 1px;
    transition: all .3s linear;
}

.profile_page .container .profile_second .profile_second_info .profile_second_btns .btn2 {
    background-color: #f8d511;
    color: #000000;
    margin: 5px 0px;
}

.profile_page .container .profile_second .profile_second_info .profile_second_btns .btn2:hover {
    opacity: .7;
}

@media (max-width: 768px) {
    .profile_page .container {
        width: auto;
    }
}

@media (max-width: 558px) {
    .profile_page .container {
        width: 80%;
    }

    .profile_page .container .profile_frist .profile_img_container .profile_img {
        width: 4.5rem;
        height: 4.5rem;
    }

    .profile_page .container .profile_frist .profile_img_container .profile_img_update {
        width: 6rem;
        font-size: 12px;
    }

    .profile_page .container .profile_frist .profile_info_creator h3 {
        font-size: 16px;
    }

    .profile_page .container .profile_frist .profile_info_creator p {
        font-size: 13px;
    }

    .profile_page .container .profile_frist .profile_info_creator p .subscription_btn {
        width: 6rem;
        font-size: 12px;
    }

    .profile_page .container .profile_frist .profile_info_creator .profile_info_creator_btns .btn1 {
        width: 6rem;
        font-size: 12px;
    }

    .profile_page .container .profile_frist .profile_info_creator .profile_info_creator_btns .btn2 {
        width: 6rem;
        font-size: 12px;
    }
}