.regiter_page{
    height: 100%;
    width: 100%;
    padding-top: 50px;
}

.regiter_page.active{
    background-color: #3c3c3c;
}

.regiter_page .container{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.regiter_page .regiter_page_head{
    text-transform: uppercase;
    font-size: 24px;
    font-family: cursive;
}

.regiter_page .regiter_page_head.active{
    color: #ffffff;
}

.regiter_page .avatar{
    width: 8rem;
    height: 8rem;
    border-radius: 50%;
    overflow: hidden;
}

.regiter_page .form_content{
    display: flex;
    flex-direction: column;
    width: 20rem;
}

.regiter_page .form_content .fname.active{
    color: #ffffff;
}
.regiter_page .form_content .fnameIp.active{
    color: #ffffff;
    background-color: #3c3c3c;
    border: 1px solid #ffffff;
}

.regiter_page .form_content .email.active{
    color: #ffffff;
}
.regiter_page .form_content .emailIp.active{
    color: #ffffff;
    background-color: #3c3c3c;
    border: 1px solid #ffffff;
}

.regiter_page .form_content .mobNum.active{
    color: #ffffff;
}
.regiter_page .form_content .mobNumIp.active{
    color: #ffffff;
    background-color: #3c3c3c;
    border: 1px solid #ffffff;
}

.regiter_page .form_content .password.active{
    color: #ffffff;
}
.regiter_page .form_content .passwordIp.active{
    color: #ffffff;
    background-color: #3c3c3c;
    border: 1px solid #ffffff;
}

.regiter_page .form_content .avatarlogo{
    height: auto;
    border-radius: 0%;
    color: #3c3c3c;
}
.regiter_page .form_content .avatarlogo.active{
    color: #ffffff;
}
.regiter_page .form_content .avatarIp.active{
    color: #ffffff;
    background-color: #3c3c3c;
    border: 1px solid #ffffff;
}

.regiter_page .form_content label{
    margin-top: 10px;
    font-size: 16px;
    margin-bottom: 5px;
}

.regiter_page .form_content input{
    width: 100%;
    font-size: 15px;
    padding: 10px 5px;
    border: 1px solid #000000;
    border-radius: 5px;
}

.regiter_page .form_content input:focus{
    box-shadow: 0rem 0rem .8rem 0rem #f8d511;
}

.regiter_page button{
    margin-top: 10px;
    background-color: #f8d511;
    color: #000000;
    padding: 10px 14px;
    font-size: 16px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    font-weight: 500;
}

.regiter_page button:hover{
    opacity: .7;
}

.regiter_page .navigate span a{
    text-transform: capitalize;
    color: #fa8f03;
    font-weight: 600;
    font-size: 16px;
    cursor: pointer;
    text-decoration: underline;
    text-decoration: none;
}

.regiter_page .navigate.active{
    color: #ffffff;
}