.footer {
    background-color: #4e4c4c;
    color: #f8d511;
    height: 9rem;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.footer.active {
    background-color: #ffffff;
    color: #4e4c4c;
}

.footer .footer_header {
    text-transform: capitalize;
    font-size: 25px;
    font-family: cursive;
    font-weight: 600;
}

.footer .footer_des {
    text-transform: capitalize;
    font-size: 15px;
    font-family: cursive;
    font-weight: 600;
}