.home_page {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    padding-top: 50px;
}

.home_page.active {
    background-color: #3c3c3c;
}

.home_page .left_div {
    text-align: right;
}

.home_page .left_div .home_page_header {
    font-weight: 700;
    text-transform: uppercase;
    color: #3c3c3c;
}

.home_page .left_div .home_page_header.active {
    color: #ffffff;
}

.home_page .left_div .home_page_des {
    text-transform: capitalize;
    font-size: 18px;
    color: #3c3c3c;
}

.home_page .left_div .home_page_des.active {
    color: #ffffff;
}

.home_page .left_div button {
    padding: 12px 16px;
    text-transform: capitalize;
    font-size: 18px;
    color: #161616;
    background-color: #f8d511;
    border: none;
    font-family: cursive;
    border-radius: 7px;
    cursor: pointer;
}

.home_page .left_div button:hover {
    opacity: .7;
}

.home_page .right_div {
    width: 20rem;
    height: 20rem;
}

.home_page .right_div img {
    width: 100%;
    height: 100%;
    margin-left: 10px;
    border-radius: 1rem;
    overflow: hidden;
    animation: updown 2s infinite;
}

@keyframes updown {
    0%,
    100% {
        transform: translateY(0px);
    }

    50% {
        transform: translateY(20px);
    }
}

@media (max-width: 768px) {
    .home_page {
        flex-direction: column;
    }

    .home_page .left_div {
        text-align: center;
    }

    .home_page .right_div {
        margin-top: 20px;
    }
}

@media (max-width: 580px) {
    .home_page {
        margin-top: 10px;
    }
}