.piechart_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: auto;
    height: auto;
}

.piechart {
    width: 100%;
    height: 100%;
}

@media (max-width: 991px) {
    .piechart_container {
        margin-top: 2rem;
    }

    .piechart {
        width: auto;
        height: auto;
    }
}


@media (max-width: 558px) {
    .piechart_container {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 8rem;
        height: auto;
    }

    .piechart {
        width: auto;
        height: auto;
    }
}

@media (max-width: 400px) {
    .piechart_container {
        width: 4rem;
    }

    .piechart {
        width: auto;
        height: auto;
    }
}