.video_list_page {
    height: auto;
    width: 100%;
}

.video_list_page.active {
    background-color: #3c3c3c;
}

.video_list_page .video_list_page_container {
    height: auto;
    width: 100%;
    display: flex;
}

/* Left Panel */
.video_list_page .video_list_page_container .video_list_page_left {
    height: 100%;
    width: 100%;
    flex: 9;
    padding-top: 70px;
}

.video_list_page .video_list_page_container .video_list_page_left .video_list_page_left_container {
    height: 100%;
    width: 100%;
}

.video_list_page .video_list_page_container .video_list_page_left .video_list_page_left_container .video_list_page_left_video_box {
    height: 100%;
    width: 100%;
}

.video_list_page .video_list_page_container .video_list_page_left .video_list_page_left_container .video_list_page_left_video_box video {
    width: 100%;
    height: 100%;
}

.video_list_page .video_list_page_container .video_list_page_left .video_list_page_left_container .video_list_page_head {
    margin-left: 10px;
    font-size: 30px;
}

.video_list_page .video_list_page_container .video_list_page_left .video_list_page_left_container .video_list_page_head.active {
    color: #ffffff;
}

.video_list_page .video_list_page_container .video_list_page_left .video_list_page_left_container .video_list_page_des {
    margin: 0px 10px;
    font-size: 20px;
}

.video_list_page .video_list_page_container .video_list_page_left .video_list_page_left_container .video_list_page_des.active {
    color: #ffffff;
}

/* Right Panel */
.video_list_page .video_list_page_container .video_list_page_right {
    flex: 3;
    box-shadow: 0rem 0rem 0.8rem 0rem #807e7e;
    padding-top: 62px;
    min-height: 100vh;
    width: 100%;
    overflow-y: scroll;

}

.video_list_page .video_list_page_container .video_list_page_right::-webkit-scrollbar {
    width: 0.4rem;
}

.video_list_page .video_list_page_container .video_list_page_right::-webkit-scrollbar-track {
    background: transparent;
}

.video_list_page .video_list_page_container .video_list_page_right::-webkit-scrollbar-thumb {
    background-color: #807e7e;
    border-radius: 5rem;
    cursor: pointer;
}

.video_list_page .video_list_page_container .video_list_page_right .video_list_page_container_right_lists {
    color: #4d4c4c;
}

.video_list_page .video_list_page_container .video_list_page_right .video_list_page_container_right_lists:hover {
    background-color: #4d4c4c;
    color: #f8f6f6;
}

.video_list_page .video_list_page_container .video_list_page_right .video_list_page_container_right_lists.active {
    color: #ffffff;
}

.video_list_page .video_list_page_container .video_list_page_right .video_list_page_container_right_lists.active:hover {
    background-color: #f8f8f8;
    color: #4d4c4c;
}

.no_video.active {
    color: #ffffff;
}

@media screen and (max-width: 558px) {
    .video_list_page .video_list_page_container {
        flex-direction: column;
    }

    .video_list_page .video_list_page_container .video_list_page_left {
        flex: 1;
    }

    .video_list_page .video_list_page_container .video_list_page_right {
        flex: 1;
    }
}