.request_new_course_page{
    height: 100vh;
    width: 100%;
    padding-top: 50px;
}

.request_new_course_page.active{
    background-color: #3c3c3c;
}

.request_new_course_page .container{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.request_new_course_page .request_new_course_page_head{
    text-transform: uppercase;
    font-size: 24px;
    font-family: cursive;
}

.request_new_course_page .request_new_course_page_head.active{
    color: #ffffff;
}

.request_new_course_page .avatar{
    width: 8rem;
    height: 8rem;
    border-radius: 50%;
    overflow: hidden;
}

.request_new_course_page .form_content{
    display: flex;
    flex-direction: column;
    width: 20rem;
}

.request_new_course_page .form_content .fname.active{
    color: #ffffff;
}
.request_new_course_page .form_content .fnameIp.active{
    color: #ffffff;
    background-color: #3c3c3c;
    border: 1px solid #ffffff;
}

.request_new_course_page .form_content .email.active{
    color: #ffffff;
}
.request_new_course_page .form_content .emailIp.active{
    color: #ffffff;
    background-color: #3c3c3c;
    border: 1px solid #ffffff;
}

.request_new_course_page .form_content .coursename.active{
    color: #ffffff;
}
.request_new_course_page .form_content .coursenameIp.active{
    color: #ffffff;
    background-color: #3c3c3c;
    border: 1px solid #ffffff;
}

.request_new_course_page .form_content label{
    margin-top: 10px;
    font-size: 16px;
    margin-bottom: 5px;
}

.request_new_course_page .form_content input{
    width: 100%;
    font-size: 15px;
    padding: 10px 5px;
    border: 1px solid #000000;
    border-radius: 5px;
}

.request_new_course_page .form_content input:focus{
    box-shadow: 0rem 0rem .8rem 0rem #f8d511;
}

.request_new_course_page .form_content textarea{
    width: 100%;
    font-size: 15px;
    padding: 10px 5px;
    border: 1px solid #000000;
    border-radius: 5px;
}

.request_new_course_page .form_content textarea:focus{
    box-shadow: 0rem 0rem .8rem 0rem #f8d511;
}

.request_new_course_page button{
    margin-top: 10px;
    background-color: #f8d511;
    color: #000000;
    padding: 10px 14px;
    font-size: 16px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    font-weight: 500;
}

.request_new_course_page button:hover{
    opacity: .7;
}