.aboutus_page {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 70px;
    padding-bottom: 20px;
}

.aboutus_page.active {
    background-color: #3c3c3c;
}

.aboutus_page .container {
    width: 40rem;
    border-radius: 10px;
    box-shadow: 0rem 0rem 0.6rem 0rem #a1a1a1;
}

.aboutus_page .container .aboutus_page_title {
    font-size: 25px;
    font-weight: 600;
    font-family: cursive;
    text-transform: capitalize;
    padding-left: 10px;
}

.aboutus_page .container .aboutus_page_title.active {
    color: #ffffff;
}

.aboutus_page .container .about_frist {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 11rem;
    padding: 10px;
}

.aboutus_page .container .about_frist .aboutus_img {
    width: 15rem;
    height: 11rem;
}

.aboutus_page .container .about_frist .aboutus_img img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
}

.aboutus_page .container .about_frist .aboutus_info_creator {
    padding-left: 10px;
}

.aboutus_page .container .about_frist .aboutus_info_creator .aboutus_info_creator_header {
    font-size: 25px;
    font-weight: 600;
    font-family: cursive;
    text-transform: capitalize;
}

.aboutus_page .container .about_frist .aboutus_info_creator .aboutus_info_creator_header.active {
    color: #ffffff;
}

.aboutus_page .container .about_frist .aboutus_info_creator .aboutus_info_creator_des {
    text-transform: capitalize;
    font-size: 14px;
}

.aboutus_page .container .about_frist .aboutus_info_creator .aboutus_info_creator_des.active {
    color: #ffffff;
}

.aboutus_page .container .about_second {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.aboutus_page .container .about_second .about_second_des {
    text-transform: capitalize;
    font-size: 14px;
}

.aboutus_page .container .about_second .about_second_des.active {
    color: #ffffff;
}

.aboutus_page .container .about_second .about_second_btn {
    text-transform: capitalize;
    font-size: 14px;
    border: none;
    border-radius: 5px;
    background-color: #eee7e7;
    color: #ff6702;
    width: 9rem;
    height: 3rem;
    cursor: pointer;
}

.aboutus_page .container .about_second .about_second_btn:hover {
    letter-spacing: .5px;
    transition: all .3s linear;
}

.aboutus_page .container .about_third {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
}

.aboutus_page .container .about_third .about_third_video {
    width: auto;
    height: 22rem;
}

.aboutus_page .container .about_third .about_third_video .about_third_video_content {
    width: 100%;
    height: 100%;
    border-radius: 10px;
}

.aboutus_page .container .about_third .about_third_term_condition {
    padding: 10px;
}

.aboutus_page .container .about_third .about_third_term_condition .about_third_term_condition_title {
    font-size: 25px;
    font-weight: 600;
    font-family: cursive;
    text-transform: capitalize;
}

.aboutus_page .container .about_third .about_third_term_condition .about_third_term_condition_title.active {
    color: #ffffff;
}

.term_condition_info {
    font-family: cursive;
}

.term_condition_info.active {
    color: #ffffff;
}

@media (max-width: 768px) {
    .aboutus_page .container {
        width: auto;
        margin: 0px 10px;
    }

    .aboutus_page .container .about_third .about_third_video {
        height: auto;
    }
}

@media (max-width: 558px) {
    .aboutus_page .container .about_frist .aboutus_img {
        width: 14rem;
        height: auto;
    }

    .aboutus_page .container .about_frist .aboutus_info_creator h3 {
        font-size: 18px;
    }
}