.payment_success_container {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.payment_success_container.active {
    background-color: #3c3c3c;
}

.payment_success_container .payment_success_container_head {
    font-size: 30px;
    font-family: cursive;
    text-transform: capitalize;
    display: flex;
    align-items: center;
    justify-content: center;
}

.payment_success_container .payment_success_container_head.active {
    color: #ffffff;
}

.payment_success_container .payment_success_container_ref {
    font-size: 20px;
    font-family: cursive;
}

.payment_success_container .payment_success_container_ref.active {
    color: #ffffff;
}

.payment_success_container a {
    font-size: 13px;
    font-family: cursive;
    color: #000000;
}

.payment_success_container_link.active{
    color: #ffffff;
}