.drawer_web{
    width: 250px;
    height: 100%;
    padding: 10px;
}

.drawer_web.active{
    background-color: #3c3c3c;
}

.drawer_web .drawer_web_header{
    display: flex;
    justify-content: center;
    font-size: 30px;
    text-transform: capitalize;
}

.drawer_web .drawer_web_header.active{
    color: #ffffff;
}

.divider{
    background-color: #3c3c3c;
}

.divider.active{
    background-color: #ffffff;
}

.drawer_web li{
    font-size: 20px;
    text-transform: capitalize;
}

.drawer_web_list_item{
    color: #3c3c3c;
}

.drawer_web_list_item.active{
    background-color: #3c3c3c;
    color: #e6e6e6;
}

.drawer_web_list_item:hover{
    background-color: #e6e6e6;
    color: #3c3c3c;
    /* letter-spacing: 3px; */
}
.drawer_web_list_item.active:hover{
    background-color: #e6e6e6;
    color: #3c3c3c;
    /* letter-spacing: 3px; */
}

.drawer_web .drawer_web_btns{
    position: fixed;
    bottom: 0;
}

.drawer_web .drawer_web_btns .btns_container{
    display: flex;
    align-items: center;
}

.drawer_web .drawer_web_btns .btns_container .btn1{
    width: 7rem;
    height: 3rem;
    text-transform: capitalize;
    background-color: #f8d511;
    color: #000000;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
}

.drawer_web .drawer_web_btns .btns_container .btn1:hover{
    letter-spacing: 1px;
    transition: all .3s linear;
}

.drawer_web .drawer_web_btns .btns_container .btn2{
    width: 7rem;
    height: 3rem;
    text-transform: capitalize;
    background-color: #eee7e7;
    color: #ff6702;
    margin-left: 15px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.drawer_web .drawer_web_btns .btns_container .btn2:hover{
    letter-spacing: 1px;
    transition: all .3s linear;
}

.drawer_web .drawer_web_btns .btn_dashboard{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 4rem;
}

.drawer_web .drawer_web_btns .btn_dashboard .btn3{
    width: 7.5rem;
    height: 3rem;
    text-transform: capitalize;
    background-color: #eee7e7;
    color: #ff6702;
    margin-left: 15px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.drawer_web .drawer_web_btns .btn_dashboard .btn3:hover{
    letter-spacing: 1px;
    transition: all .3s linear;
}