.course_page{
    min-height: 95vh;
    width: 100%;
    padding-bottom: 10px;
    padding-top: 50px;
}

.course_page.active{
    background-color: #3c3c3c;
}

.course_page .course_page_container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.course_page .course_page_container .course_page_header{
    font-size: 30px;
    font-family: cursive;
}

.course_page .course_page_container .course_page_header.active{
    color: #ffffff;
}

.course_page .course_page_container .course_input{
    width: 42rem;
    height: 100%;
}

.course_page .course_page_container .course_input .course_page_input{
    width: 100%;
    height: 100%;
    padding: 10px 8px;
    font-size: 16px;
    border-radius: 5px;
    border: 1px solid #000000;
}

.course_page .course_page_container .course_input .course_page_input.active{
    background-color: #3c3c3c;
    color: #ffffff;
    border: 1px solid #ffffff;
}

.course_page .course_page_container .course_input .course_page_input.active::placeholder{
    color: #ffffff;
    background-color: #3c3c3c;
}

.course_page .course_page_container .course_input .course_page_input:focus{
    box-shadow: 0rem 0rem .8rem 0rem #f8d511;
}

.course_page .course_page_container .course_page_btns{
    width: 42rem;
    height: 100%;
    margin: 10px 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.course_page .course_page_container .course_page_btns button{
    height: 2rem;
    text-transform: capitalize;
    background-color: #f8d511;
    color: #000000;
    border: none;
    border-radius: 5px;
    font-size: 14px;
    cursor: pointer;
}

.course_page .course_page_container .course_page_btns button:hover{
    opacity: .7;
}

.course_page .course_page_container .course_page_course_box{
    width: 42rem;
    height: 100%;
    margin: 20px 0px;
}

.course_page .course_page_container .course_page_course_box .course_page_course_list{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(18rem, 1fr));
    gap: 1rem;
    width: 100%;
    height: 100%;
}

.course_page .course_page_container .course_page_course_box .course_page_course_list .course_box{
    width: 20rem;
    height: 100%;
    border: 1px solid #000000;
    border-radius: 5px;
    margin-bottom: 10px;
    transition: 0.5s ease-in-out;
}

.course_page .course_page_container .course_page_course_box .course_page_course_list .course_box:hover{
    transform: scale(1.05);
    transition: 0.5s ease-in-out;
    box-shadow: 0rem 0rem 0.8rem #323131;
}

.course_page .course_page_container .course_page_course_box .course_page_course_list .course_box.active:hover{
    box-shadow: 0rem 0rem 0.8rem rgb(137, 137, 137);
    transition: 0.5s ease-in-out;
}

.course_page .course_page_container .course_page_course_box .course_page_course_list .course_box .course_img{
    width: auto;
    height: 10rem;
    position: relative;
}

.course_page .course_page_container .course_page_course_box .course_page_course_list .course_box .course_img img{
    width: 100%;
    height: 100%;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    object-fit: cover;
}

.course_page .course_page_container .course_page_course_box .course_page_course_list .course_box .course_content{
    padding: 10px;
}

.course_page .course_page_container .course_page_course_box .course_page_course_list .course_box .course_content .course_content_title{
    font-size: 18px;
    font-family: cursive;
    margin-top: -8px;
}

.course_page .course_page_container .course_page_course_box .course_page_course_list .course_box .course_content .course_content_title.active{
    color: #ffffff;
}

.course_page .course_page_container .course_page_course_box .course_page_course_list .course_box .course_content .course_content_description{
    font-size: 15px;
    margin-top: -8px;
    font-family: cursive;
}

.course_page .course_page_container .course_page_course_box .course_page_course_list .course_box .course_content .course_content_description.active{
    color: #ffffff;
}

.course_page .course_page_container .course_page_course_box .course_page_course_list .course_box .course_content .course_content_creater{
    font-size: 15px;
    margin-top: -8px;
    font-family: cursive;
}

.course_page .course_page_container .course_page_course_box .course_page_course_list .course_box .course_content .course_content_creater.active{
    color: #ffffff;
}

.course_page .course_page_container .course_page_course_box .course_page_course_list .course_box .course_content .course_content_lecture{
    font-size: 16px;
    font-weight: 500;
    font-family: cursive;
}

.course_page .course_page_container .course_page_course_box .course_page_course_list .course_box .course_content .course_content_lecture.active{
    color: #ffffff;
}

.course_page .course_page_container .course_page_course_box .course_page_course_list .course_box .course_content .course_content_btns{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.course_page .course_page_container .course_page_course_box .course_page_course_list .course_box .course_content .course_content_btns .btn1{
    width: 7rem;
    height: 3rem;
    font-size: 14px;
    text-transform: capitalize;
    background-color: #f8d511;
    font-weight: 500;
    color: #000000;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.course_page .course_page_container .course_page_course_box .course_page_course_list .course_box .course_content .course_content_btns .btn1:hover{
    letter-spacing: 1px;
    transition: all .3s linear;
}

.course_page .course_page_container .course_page_course_box .course_page_course_list .course_box .course_content .course_content_btns .btn2{
    width: 7rem;
    height: 3rem;
    font-size: 14px;
    text-transform: capitalize;
    background-color: #eee7e7;
    font-weight: 500;
    color: #ff6702;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.course_page .course_page_container .course_page_course_box .course_page_course_list .course_box .course_content .course_content_btns .btn2:hover{
    letter-spacing: 1px;
    transition: all .3s linear;
}

@media (max-width: 768px) {
    .course_page .course_page_container .course_input{
        width: 30rem;
    }

    .course_page .course_page_container .course_page_btns{
        width: 30rem;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(13rem, 1fr));
        gap: 1rem;
    }

    .course_page .course_page_container .course_page_course_box{
        width: auto;
    }
}

@media (max-width: 550px) {
    .course_page .course_page_container .course_input{
        width: 20rem;
    }

    .course_page .course_page_container .course_page_btns{
        width: 20rem;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(8rem, 1fr));
        gap: 1rem;
    }

    .course_page .course_page_container .course_page_btns button{
        font-size: 12px;
    }


}