.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 50px;
    width: 100%;
    position: fixed;
}

.header .header_container_right{
    display: flex;
}

.header .header_container_right .admin_dashboard_drawer_right{
    color: #4e4c4c;
    background-color: #f8d511;
    margin-right: 5px;
    display: none;
}

.header .header_container_right .admin_dashboard_drawer_right:hover{
    opacity: .7;
}

.header button{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    padding: 7px;
    border: none;
    cursor: pointer;
}

.drawer_btn{
    color: #4e4c4c;
    background-color: #f8d511;
    margin-left: 5px;
}

.drawer_btn:hover{
    opacity: .7;
}

.dark_mode_btn{
    color: #4e4c4c;
    background-color: #f8d511;
    margin-right: 5px;
}

.dark_mode_btn:hover{
    opacity: .7;
}

@media (max-width: 768px) {
    .header .header_container_right .admin_dashboard_drawer_right{
        display: flex;
    }
}