.dashboard_page {
    min-height: auto;
    width: 100%;
}

.dashboard_page.active {
    background-color: #3c3c3c;
}

.dashboard_page .dashboard_page_container {
    height: 100%;
    width: 100%;
    display: flex;
}

/* Left Panel */
.dashboard_page .dashboard_page_container .dashboard_page_container_left {
    height: 100%;
    width: 80%;
    flex: 9;
    padding-top: 70px;
    min-height: 100vh;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    padding-bottom: 20px;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .create_course_page_header {
    text-transform: uppercase;
    font-size: 24px;
    font-family: cursive;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .create_course_page_header.active {
    color: #ffffff;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .form_content {
    display: flex;
    flex-direction: column;
    width: 20rem;
}

.courseTitle.active {
    color: #ffffff;
}
.dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .form_content .courseTitleIp.active {
    background-color: #3c3c3c;
    color: #ffffff;
    border: 1px solid #ffffff;
}

.courseDescription.active {
    color: #ffffff;
}
.dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .form_content .courseDescriptionIp.active {
    background-color: #3c3c3c;
    color: #ffffff;
    border: 1px solid #ffffff;
}

.courseCreater.active {
    color: #ffffff;
}
.dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .form_content .courseCreaterIp.active {
    background-color: #3c3c3c;
    color: #ffffff;
    border: 1px solid #ffffff;
}

.courseCategory.active {
    color: #ffffff;
}
.dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .form_content .courseCategoryIp.active {
    background-color: #3c3c3c;
    color: #ffffff;
    border: 1px solid #ffffff;
}

.coursePrice.active {
    color: #ffffff;
}
.dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .form_content .coursePriceIp.active {
    background-color: #3c3c3c;
    color: #ffffff;
    border: 1px solid #ffffff;
}

.courseDiscount.active {
    color: #ffffff;
}
.dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .form_content .courseDiscountIp.active {
    background-color: #3c3c3c;
    color: #ffffff;
    border: 1px solid #ffffff;
}

.courseImg.active {
    color: #ffffff;
}
.dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .form_content .courseImgIp.active {
    background-color: #3c3c3c;
    color: #ffffff;
    border: 1px solid #ffffff;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .form_content label {
    margin-top: 10px;
    font-size: 16px;
    margin-bottom: 5px;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .form_content input {
    width: 100%;
    font-size: 15px;
    padding: 10px 5px;
    border: 1px solid #000000;
    border-radius: 5px;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .form_content input:focus {
    box-shadow: 0rem 0rem .8rem 0rem #f8d511;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .form_content select {
    width: 100%;
    font-size: 15px;
    padding: 10px 5px;
    border: 1px solid #000000;
    border-radius: 5px;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .form_content select:focus {
    box-shadow: 0rem 0rem .8rem 0rem #f8d511;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .form_content textarea {
    width: 100%;
    font-size: 15px;
    padding: 10px 5px;
    border: 1px solid #000000;
    border-radius: 5px;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .form_content textarea:focus {
    box-shadow: 0rem 0rem .8rem 0rem #f8d511;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .form_content input:focus {
    box-shadow: 0rem 0rem .8rem 0rem #f8d511;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page button {
    margin-top: 10px;
    background-color: #f8d511;
    color: #000000;
    padding: 10px 14px;
    font-size: 16px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    font-weight: 500;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page button:hover {
    opacity: .7;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_right {
    flex: 3;
    box-shadow: 0rem 0rem 0.8rem 0rem #807e7e;
    padding-top: 62px;
    min-height: 100vh;
    width: 100%;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_right::-webkit-scrollbar {
    width: 0.5rem;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_right::-webkit-scrollbar-track {
    background: transparent;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_right::-webkit-scrollbar-thumb {
    background-color: #807e7e;
    border-radius: 5rem;
}

@media (max-width: 500px) {
    .dashboard_page .dashboard_page_container .dashboard_page_container_right .dashboard_page_container_right_lists {
        font-size: 10px;
    }

    .dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .form_content {
        width: 15rem;
    }

    .dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page h3 {
        font-size: 18px;
    }

    .dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .form_content label {
        font-size: 12px;
    }

    .dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .form_content input {
        font-size: 12px;
        padding: 6px 3px;
    }

    .dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .form_content select {
        font-size: 12px;
        padding: 6px 3px;
    }

    .dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .form_content textarea {
        font-size: 12px;
        padding: 6px 3px;
    }
}

@media (max-width: 400px) {
    .dashboard_page .dashboard_page_container .dashboard_page_container_right .dashboard_page_container_right_lists {
        font-size: 6px;
    }

    .dashboard_page .dashboard_page_container .dashboard_page_container_left .create_course_page .form_content {
        width: 13.5rem;
    }
}