.dashboard_page{
    height: auto;
    width: 100%;
}

.dashboard_page.active {
    background-color: #3c3c3c;
}

.dashboard_page .dashboard_page_container{
    height: 100%;
    width: 100%;
    display: flex;
}

/* Left Panel */
.dashboard_page .dashboard_page_container .dashboard_page_container_left{
    height: 100%;
    width: 80%;
    flex: 9;
    padding-top: 70px;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .all_course_page{
    width: 100%;
    height: 100%;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .all_course_page .container{
    padding: 10px;
    margin-top: -20px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: auto;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .all_course_page .container .all_course_page_header{
    font-size: 25px;
    font-family: cursive;
    font-weight: 600;
    text-align: left;
    text-transform: capitalize;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .all_course_page .container .all_course_page_header.active{
    color: #ffffff;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .all_course_page .container .table_container{
    /* width: 80%; */
    height: 100%;
    border-collapse: collapse;
    border-radius: 2px;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .all_course_page .container .table_container .table_head{
    background-color: #494949;
    color: #ffffff;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .all_course_page .container .table_container .table_head .table_head_row .table_head_row_cell{
    padding: 10px 5px;
    font-size: 18px;
    font-weight: 600;
    font-family: cursive;
    text-transform: capitalize;
    text-align: center;
    border: 1px solid #ffffff;
    color: #ffffff;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .all_course_page .container .table_container .table_body .table_body_row .table_body_row_cell{
    padding: 10px 5px;
    text-align: center;
    font-size: 15px;
    font-family: cursive;
    text-transform: capitalize;
    border: 1px solid #000000;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .all_course_page .container .table_container .table_body .table_body_row .table_body_row_cell.active{
    color: #ffffff;
    border: 1px solid #ffffff;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .all_course_page .container .table_container .table_body .table_body_row .td_btns{
    width: auto;
    height: auto;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .all_course_page .container .table_container .table_body .table_body_row .td_btns .btn1{
    width: 4rem;
    height: 2rem;
    font-size: 11px;
    font-family: cursive;
    text-transform: capitalize;
    border: none;
    border-radius: 5px;
    background-color: #f8d511;
    color: #000000;
    cursor: pointer;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .all_course_page .container .table_container .table_body .table_body_row .td_btns .btn1:hover{
    opacity: .7;
    box-shadow: 0rem 0rem .8rem 0rem #f8d511;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .all_course_page .container .table_container .table_body .table_body_row .td_btns .btn2{
    width: 2rem;
    height: 2rem;
    font-family: cursive;
    text-transform: capitalize;
    border: none;
    border-radius: 5px;
    background-color: #ffffff;
    color: #f8d511;
    cursor: pointer;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_left .all_course_page .container .table_container .table_body .table_body_row .td_btns .btn2:hover{
    opacity: .7;
    box-shadow: 0rem 0rem .8rem 0rem #f8d511;
}

/* Right Panel */
.dashboard_page .dashboard_page_container .dashboard_page_container_right{
    flex: 3;
    box-shadow: 0rem 0rem 0.8rem 0rem #807e7e;
    padding-top: 62px;
    /* min-height: 100vh; */
    width: 100%;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_right::-webkit-scrollbar {
    width: 0.5rem;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_right::-webkit-scrollbar-track {
    background: transparent;
}

.dashboard_page .dashboard_page_container .dashboard_page_container_right::-webkit-scrollbar-thumb {
    background-color: #807e7e;
    border-radius: 5rem;
}

@media (max-width: 558px) {
    .dashboard_page .dashboard_page_container .dashboard_page_container_left{
        flex: 1;
    }

    .dashboard_page .dashboard_page_container .dashboard_page_container_right {
        flex: 0;
        display: none;
    }
}