.add_videos_page {
    width: 100%;
    min-height: 95vh;
    padding-top: 60px;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.add_videos_page.active {
    background-color: #3c3c3c;
}

.add_videos_page .add_videos_page_head {
    font-size: 25px;
    font-family: cursive;
    font-weight: 600;
    text-transform: capitalize;
    text-align: center;
}

.add_videos_page .add_videos_page_head.active {
    color: #ffffff;
}

.add_videos_page .container {
    display: flex;
    justify-content: space-between;
    width: 80%;
    height: 100%;
}

.add_videos_page .container .add_videos_page_left {
    flex: 6;
    width: 100%;
    height: 100%;
    padding: 20px;
}

.add_videos_page .container .add_videos_page_left table {
    width: 100%;
    height: 100%;
    /* padding: 10px; */
    border: 1px solid #000000;
    border-collapse: collapse;
}

.add_videos_page .container .add_videos_page_left table thead {
    background-color: #4b4a4a;
}

.add_videos_page .container .add_videos_page_left table thead tr th {
    color: #ffffff;
    text-transform: capitalize;
    padding: 10px 15px;
    text-align: center;
    border: 1px solid #ffffff;
}

.add_videos_page .container .add_videos_page_left table tbody .table_row_body .table_row_body_cell {
    text-align: center;
    text-transform: capitalize;
    border: 1px solid #000000;
    padding: 5px;
}

.add_videos_page .container .add_videos_page_left table tbody .table_row_body .table_row_body_cell.active {
    color: #ffffff;
    border: 1px solid #ffffff;
}

.add_videos_page .container .add_videos_page_left table tbody .table_row_body .table_row_body_cell.active > a {
    color: #ffffff;
}

.add_videos_page .container .add_videos_page_left table tbody .table_row_body .table_row_body_cell video {
    width: 20rem;
    height: auto;
}

.add_videos_page .container .add_videos_page_left table tbody .table_row_body .table_row_body_cell button {
    width: 2rem;
    height: 2rem;
    cursor: pointer;
    background-color: #f8d511;
    color: #000000;
    border: none;
    border-radius: 50%;
}

.add_videos_page .container .add_videos_page_left table tbody .table_row_body .table_row_body_cell button:hover {
    box-shadow: 0rem 0rem 0.5rem 0rem #f8d511;
    opacity: .7;
}

.add_videos_page .container .add_videos_page_right {
    flex: 4;
    width: 100%;
    height: 100%;
    padding: 20px;
}

.add_videos_page .container .add_videos_page_right form {
    width: 100%;
    height: 100%;
    box-shadow: 0rem 0rem 0.5rem 0rem #000000;
    padding: 20px;
    border-radius: 10px;
}

.add_videos_page .container .add_videos_page_right form .form_container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.add_videos_page .container .add_videos_page_right form .form_container .videoTitle.active{
    color: #ffffff;
}
.add_videos_page .container .add_videos_page_right form .form_container .videoTitleIp.active{
    color: #ffffff;
    background-color: #3c3c3c;
    border: 1px solid #ffffff;
}

.add_videos_page .container .add_videos_page_right form .form_container .videoUrl.active{
    color: #ffffff;
}
.add_videos_page .container .add_videos_page_right form .form_container .videoUrlIp.active{
    color: #ffffff;
    background-color: #3c3c3c;
    border: 1px solid #ffffff;
}

.add_videos_page .container .add_videos_page_right form .form_container label {
    margin-top: 10px;
    font-size: 16px;
    font-weight: 600;
    font-family: cursive;
    margin-bottom: 5px;
    text-transform: capitalize;
}

.add_videos_page .container .add_videos_page_right form .form_container input {
    width: 100%;
    font-size: 15px;
    padding: 10px 5px;
    border: 1px solid #000000;
    border-radius: 5px;
}

.add_videos_page .container .add_videos_page_right form .form_container input:focus {
    box-shadow: 0rem 0rem .8rem 0rem #f8d511;
}

.add_videos_page .container .add_videos_page_right form button {
    width: 7rem;
    height: 3rem;
    font-size: 16px;
    text-transform: capitalize;
    font-family: cursive;
    background-color: #f8d511;
    color: #000000;
    margin-top: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.add_videos_page .container .add_videos_page_right form button:hover {
    opacity: .7;
    box-shadow: 0rem 0rem 0.5rem 0rem #f8d511;
}

@media (max-width: 768px) {
    .add_videos_page .container {
        flex-direction: column;
    }

    .add_videos_page .container .add_videos_page_left {
        flex: 1;
        width: auto
    }

    .add_videos_page .container .add_videos_page_right {
        flex: 1;
        width: auto;
        /* background-color: #f8d511; */
    }
}

@media (max-width: 558px) {
    .add_videos_page .container {
        flex-direction: column;
        width: 100%;
    }

    .add_videos_page .container .add_videos_page_right form .form_container {
        width: auto;
    }

    .add_videos_page .container .add_videos_page_left table tbody tr td video {
        width: 15rem;
    }

    .add_videos_page .container .add_videos_page_right {
        margin-bottom: 10px;
    }

    .add_videos_page .container .add_videos_page_right {
        width: 80%;
    }

    .add_videos_page .container .add_videos_page_right form .form_container input {
        font-size: 12px;
        padding: 6px 3px;
        width: 100%;
    }
}